.input {
    display: flex;
    align-items: baseline;
    width: 660px;
    margin-right: none !important;
    border-right: none;
    border-top-right-radius: 0px !important;
    border: 1px solid white;
    border-bottom-right-radius: 0px !important;
}

.iconGroup {
    margin-left: auto;
}

.input:focus {
    border-color: rgba(117, 117, 117, 0.5);
    box-shadow: rgb(4 17 29 / 25%) 0px 0px 8px 0px;
}

.navigation {
    background-color: #629390ec;
    padding-top: 0px;
    padding-bottom: 0px;

}

.inputDiv {
    position: relative;
    display: flex;
    align-items: center;
    margin-right: none;
    margin-left: 50px;
}



.searchBtn {
    background-color: #fff;
    border: none;
    outline: none;
    height: 100%;
    border-radius: 0.25rem;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
}

.hoverItem a:hover {
    color: #c7c6c6 !important;
    transition:linear
}


@media screen and (max-width: 1600px) {
    .inputDiv {
        margin : 0;
        margin-left: 50px;
    }
    .input {
        width: 500px;
    }
  
}
@media screen and (max-width: 1400px) {
    .inputDiv {
        margin : 0;
        margin-left: 50px;
    }
    .input {
        width: 100%;
        margin: 0;
    }
}

@media (max-width: 977px) {
    .inputDiv {
        display: none;
    }
    .iconGroup {
        flex-direction: row;
    }
    .iconGroup .navLink {
        margin: 0 8px;
    }
}