.carousel {
    display: flex;
    flex-direction: row;
}

.outerCard {
    margin: 0 20px;
    box-shadow: -4px 3px 15px -8px rgba(0, 0, 0, .5);
    border-radius: .5rem;
    cursor: pointer;
}

.cardImg {
    border-radius: 50%;
    width: 75px;
    height: 75px;
    background-color: rgb(19, 19, 19);
}

.innerCard {
    height: 200px;
    width: 100%;
    background-color: #629390;
   align-items: center;
   border-radius: .5rem;
   text-align: center;
   padding: 15px 0;
}

.cardtitle {
    color: rgba(255, 255, 255, 0.876);
}

.react-multiple-carousel__arrow--left {
    left: calc(40% + 6px) !important
}

h3 {
    color : white;
}
