.topSection {
    width: 100%;
    font-size: 1.6rem;
}

.traitsContainer {
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 800px;
    min-width: 560px;
}

.traitsDiv {
    background-color: #8ae0db32;
    margin: 8px 15px;
    width: 250px;
    padding: 10px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid rgba(255, 255, 255, 0.548);
    border-radius: 15px;
    box-shadow:  4px 4px 20px -1px rgba(31, 68, 55, 0.616);
}


.myContainer {
    display: flex;
    padding: 100px 150px;
    color: white;
}

.mainImage {
    width: 80%;
    height: auto;
    min-width: 350px;
    margin-right: 1em;
}

.owner {
    display: flex;
    align-items: center;
    margin: 15px 0;
}

.owner img {
    width: 50px;
    border-radius: 50px;
}

.value { 
    color: #e2f3ef;
     font-size: 1.2rem;
      font-weight: bold 
    }


@media screen and (max-width: 960px) {
    .myContainer {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 50px;
    }
    .mainImage {
        margin-bottom: 40px;
    }
    .rightCol {
        margin: 0 auto;
        text-align: center;
    }

    .owner {
        justify-content: center;
    }

    .traitsContainer {
        align-items: center;
        justify-content: center;
    }

    .traitsDiv {
        margin: 8px !important;
    }
    .mainImage {
        width: 100%;
        padding: 0 150px;
    }
}

@media screen and (max-width: 1280px) {
    .traitsDiv {
        margin: 8px 4px;
    }
    .myContainer {
        padding: 50px;
    }
    .rightCol h1 {
        font-size: 12px;
    }
    mainImage {
        width: 80%;
    }
}

@media screen and (max-width: 750px) {
    .mainImage {
        padding: 20px ;
    }

    .traitsContainer {
        min-width: 100%;
    }

    .traitsDiv {
        width: 150px;
        height: 100px;
    }

    .value {
        font-size: 1em;
    }
}