.colCard {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 60%;
    margin-bottom: 10px;
    border-bottom: 1px solid rgba(202, 202, 202, 0.527);
    padding-bottom: 20px;
    color: white;
    font-size: 1.3em;
    height: 76px;
}

.heading {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 60%;
    margin-bottom: 10px;
    border-bottom: 1px solid rgb(117, 117, 117);
    padding-bottom: 20px;
    font-size: 1.1em;
    color: white;
}

.btn {
    display: inline-block;
    font-weight: 400;
    line-height: 1.5;
    color: white;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px
    solid white;
    border-radius: 500px;
    padding: 0.25rem 0.55rem;
    font-size: 14px;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    margin-left: 10px;
}

.btn:hover {
    border-color: rgb(41, 41, 41);
    color: rgb(24, 24, 24);
}

h1 {
    color: white;
}

@media (max-width: 700px) {
    .colCard {
        width: 100%;
    }
    .heading {
        width: 100%;
    }
}